import React from "react";
import Confetti from "react-confetti";

import sadSmiile from "../img/sad-face-emoji.gif";
import trophyfrom from "../img/trophy.gif";
import Header from "./Header";

export function StartMessage({ level, stars, data, setShowLevels, setStartMessage }) {
  return (
    <>
      <Header title={"מידע על השלב"} backFunction={setShowLevels} typeFunck={true} />
      <div className="message">
        <div className="message__container tartMessage">
          <div className="message__level">
            <div className="message__level__text">שלב</div>
            <div className="message__level__number">{level || 0}</div>
          </div>
          <div className="message__container__data">
            <div className="message__star">
              <span>
                כמות כוכבים נוכחית:{" "}
                <svg viewBox="0 0 24 24">
                  <path d="M12 17.27L18.18 21l-1.64-7.03L22 9.24l-7.19-.61L12 2 9.19 8.63 2 9.24l5.46 4.73L5.82 21z"></path>
                </svg>
                {stars || 0}
              </span>
            </div>
            <div className="message__data">
              <h6>על מנת לעבור שלב זה:</h6>
              <div className="message__data__mistakes">
                <div>צריך לענות על:</div> <span>{data?.succeeded || 0} שאלות</span>
              </div>
              <div className="message__data__mistakes">
                <div>מותר לטעות עד:</div> <span>{data?.mistakes || 0} טעויות</span>
              </div>
              <div className="message__data__timer">
                <div>זמן לשאלה:</div> <span>{data?.timer || 0} שניות</span>
              </div>
            </div>
          </div>
          <div className="button-style opicelAnimation" onClick={() => setStartMessage(false)} style={{ animationDelay: "400sm " }}>
            שחק עכשיו
          </div>
        </div>
      </div>
    </>
  );
}

